@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
}

.App {
  background-color: #f4faef;
}

.app-container {
  margin-top: 30px;
  min-height: 600px;
}

.section-container {
  max-width: 820px;
  margin: 0 auto;
  padding: 0 20px;
}

.section-title {
  color: #428b30;
  text-align: center;
  font-weight: 700;
}

.contact-me-now{
  text-align: center;
  margin: 20px 0;
}

.contact-me-now p {
  font-weight: 700;
}

.contact-button{
  border: none;
  background-color: #428b30;
  padding: 15px;
  color: #f4faef;
}
.navbar, .dropdown-menu{
  background-color: #a6da9a !important;
  color:#fff !important;
}

.dropdown-menu{
  border: none !important;
}
.logo-div {
  max-width: 300px;
  margin: 20px auto;
}

.logo {
  width: 100%;
}

.home-header {
  text-align: center;
  margin-bottom: 20px;
}

.home-header h1{
  font-size: 24px;
}

.home-emphasis{
  text-align: center;
  font-style: italic;
}

.intro-section {
  display: flex;
  flex-direction: row-reverse;
  margin-top: 20px;
}

.intro-text {
  width: 70%;
  margin-right: 30px;
  justify-content: space-between;
}

.greeting{
  text-align: center;
  font-weight: 700;
  font-style: italic;
}

.photo-div {
  width: 25%;
}

.intro-text {
  line-height: 25px;
}

.photo {
  width: 100%;
  border-radius: 5px;
}

.specialties-intro {
  margin-top: 40px;
}

.subtitle {
  text-align: center;
  color: #428b30;
  margin-bottom: 20px;
}

.specialty-text:not(:last-child) {
  margin-bottom: 15px;
}

.specialty-span {
  font-weight: 700;
  text-decoration: underline;
  cursor: pointer;
  color: #428b30;
  display: inline;
  font-size: 16px;
}

.verse {
  font-style: italic;
}

.how-it-works {
  margin-top: 50px;
}

.how-it-works h5 {
  font-weight: 700;
}

.works-text:not(:last-child) {
  margin-bottom: 15px;
}

@media screen and (max-width: 768px) {
  .home-header{
    margin-bottom: 30px;
  }

  .intro-section {
    display: inline;
    display: initial;
  }

  .photo-div {
    text-align: center;
    width: 100%;
  }

  .photo {
    width: 80%;
  }

  .intro-text {
    margin-top: 30px;
    width: 100%;
    margin-right: 30px;
  }
}
.about-header {
  text-align: center;
}

.verified{
  width: 150px; 
}

.about-emphasis{
  text-align: center;
  font-style: italic;
}

.img-div {
  width: 100%;
  display: flex;
  justify-content: center;
}

.about-img {
  border-radius: 5px;
  max-width: 300px;
}

.bio-details {
  margin-top: 20px;
  font-weight: 700;
}

.bio-details h1{
  font-size: 16px;
  font-weight: 700;
}

.education-experience-title {
  text-align: center;
  color: #428b30;
  margin: 40px 0 20px 0;
}

.education-experience-div {
  display: flex;
  justify-content: space-between;
}

.education-experience-subtitle {
  font-style: italic;
}

.education-experience-subtitle:not(:first-of-type) {
  margin-top: 5px;
}

.experience-div h4,
.education-div h4{
  font-weight: 700;
}
.emdr-subtitle{
  text-align: center;
}

.emdr-emdria-img-div{
  text-align: center;
  margin: 10px 0;
}

.emdr-emdria-img{
  width: 150px;
}

.emdr-qa{
  font-weight: 700;
}

.emdr-faq-section{
  background-color: #cfcfcf;
  padding: 10px; 
  border-radius: 5px;
  margin-bottom: 10px;
}

.emdr-emphasis{
  text-align: center;
  font-style: italic;
}
.fbcc-content{
  margin: 30px 0;
}

.fbcc-emphasis{
  text-align: center;
  font-style: italic;
}

.fbcc-faq-section{
  background-color: #cfcfcf;
  padding: 10px; 
  border-radius: 5px;
  margin-bottom: 10px;
}

.fbcc-qa{
  font-weight: 700;
}


.ppd-emphasis{
  text-align: center;
  font-style: italic;
}
.tfcbt-subtitle{
  text-align: center;
}

.tfcbt-emphasis{
  text-align: center;
  font-style: italic;
}

.tfcbt-content{
  margin: 30px 0;
}

.tfcbt-qa{
  font-weight: 700;
}

.tfcbt-faq-section{
  background-color: #cfcfcf;
  padding: 10px; 
  border-radius: 5px;
  margin-bottom: 10px;
}
.insurance-container {
  margin-top: 20px;
}

.insurance:not(:last-child) {
  margin-bottom: 15px;
}

.insurance-category{
  font-weight: 700;
}

@media screen and (max-width: 768px) {
  .education-experience-div{
    display: inline;
    display: initial;
  }
}
.contact-title {
  text-align: center;
  margin: 30px 0;
}

.contact-div {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.form-div,
.contact-details {
  width: 48%;
}

.form-input {
  width: 100%;
  margin-bottom: 20px;
  border: 1.5px solid #428b30;
  background-color: #f4faef;
  padding: 15px 5px;
}

.form-submit {
  width: 100%;
  border: none;
  background-color: #428b30;
  color: #f4faef;
  padding: 10px 0;
}

.contact-methods {
  margin-top: 20px;
}

.contact-icon {
  background-color: #428b30;
  padding: 10px;
  border-radius: 100%;
  color: #f4faef;
  font-size: 14px;
}

.email,
.phone-number {
  margin-bottom: 20px;
}

.phone-number,
.email-link {
  color: #428b30;
  font-size: 18px;
}

.phone-number:hover,
.email-link:hover{
  color: #428b30;
}

@media screen and (max-width: 700px) {
  .contact-div {
    display: inline;
    display: initial;
  }

  .form-div,
  .contact-details {
    width: 100%;
  }

  .contact-details {
    margin-top: 30px;
  }
}
.footer {
  background-color: #a6da9a;
  text-align: center;
  color: #428b30;
  padding: 30px 0;
  margin-top: 50px;
}

.footer a:visited {
  color: #428b30;
}

.footer-content {
  max-width: 800px;
  padding: 0 20px;
  margin: 0 auto;
}

.footer-content h3 {
  font-weight: 700;
}

.emdria-img{
  width: 150px;
  margin-right: 20px;
}

.contact-info {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  font-weight: 700;
}

.privacy-link{
  color: #428b30;
  font-weight: 700;
  font-size: 18px;
}

.separator {
  margin: 0 10px;
}

@media screen and (max-width: 768px) {
  .footer-content h3 {
    font-size: 20px;
  }
}

@media screen and (max-width: 540px) {
  .separator {
    display: none;
  }

  .contact-info {
    display: block;
  }
}
